header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 40px;
    border-bottom: 1px solid #eaeaea;

    .headerLeft {
        display: flex;
        align-items: center;
    }

    h3 {
        text-transform: uppercase;
    }
    ul {
        display: flex;

        li {
            list-style-type: none;
            margin-right: 30px;
        }

        .cart {
            display: flex;
            align-items: center;

            &:hover {
                cursor: pointer;
            }
        }
    }

    img {
        margin-right: 15px;
    }

    h3,
    p {
        margin: 0;
    }

    p {
        opacity: 0.5;
    }
}

a {
    text-decoration: none;
    color: hsl(0, 0%, 0%);

    &:active {
        color: hsl(0, 0%, 0%);
    }
}
